import React from "react";
import Design from "../../../assets/Homebanner.png";

const Homebanner = () => {
  const data = [
    {
      id: 1,
      number: 2000,
      title: "Premium Product",
    },
    {
      id: 2,
      number: 3000,
      title: "Happy Customers",
    },
    {
      id: 3,
      number: 2500,
      title: "Award Winning",
    },
  ];
  return (
    <>
      <div className="grid grid-cols-1 lg:grid-cols-2 pt-28 md:pt-20 xl:pt-0">
        {/* left part */}
        <div className="my-auto px-5 2xl:px-60 xl:px-24 lg:px-8">
          <div className="flex flex-wrap text-left ">
            <h2 className="font-semibold xl:text-3xl lg:text-2xl md:text-xl text-lg  mb-2 text-[#262626]">
              Modern Apartment In A New Residential Complex
            </h2>
            <p className="lg:text-base text-sm text-[#737373]">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Lectus
              ultrices eget non vehicula aenean.Dolor morbi neque imperdiet eu
              nisl, dis a. Enim curabitur enim, diam eget.
            </p>
          </div>
          <div className="flex justify-between mt-10 gap-4">
            {data?.map((item, index) => (
              <div key={index}>
                <div className="xl:text-3xl lg:text-2xl md:text-xl text-lg  font-bold">
                  <span>
                    {item.number}
                    <span className="text-red-600 ">+</span>
                  </span>
                </div>
                <p className="text-[#737373] lg:text-base text-sm">
                  {item.title}
                </p>
              </div>
            ))}
          </div>
        </div>
        {/* right part */}
        <div className="grid place-content-end">
          <img src={Design} alt="" className="" />
        </div>
      </div>
    </>
  );
};

export default Homebanner;
