import React from "react";
import classnames from "classnames";
import {
  usePagination,
  DOTS,
} from "../../../Hooks/usePagination/usePagination";
import "./Pagination.scss";

const Pagination = ({
  onPageChange,
  totalCount,
  siblingCount = 1,
  currentPage = 1,
  pageSize = 10,
  className,
}) => {
  const paginationRange = usePagination({
    currentPage,
    totalCount,
    siblingCount,
    pageSize,
  });

  const onNext = () => {
    onPageChange(currentPage + 1);
  };

  const onPrevious = () => {
    onPageChange(currentPage - 1);
  };

  return (
    <ul className={classnames("pagination-container", className)}>
      <li
        key="previous"
        className={classnames("pagination-item", {
          disabled: currentPage === 1,
        })}
        onClick={onPrevious}
      >
        Prev
      </li>
      {paginationRange.map((pageNumber, index) => {
        if (pageNumber === DOTS) {
          return (
            <li
              key={`dots${index}`}
              className={classnames("pagination-item dots", {
                disabled:
                  index === 0 ||
                  index === paginationRange.length - 1 ||
                  paginationRange[index - 1] === DOTS,
              })}
            >
              &#8230;
            </li>
          );
        }

        return (
          <li
            key={`page${pageNumber}`}
            className={classnames("pagination-item", {
              selected: pageNumber === currentPage,
            })}
            onClick={() => onPageChange(pageNumber)}
          >
            {pageNumber}
          </li>
        );
      })}
      <li
        key="next"
        className={classnames("pagination-item", {
          disabled: currentPage === Math.ceil(totalCount / pageSize),
        })}
        onClick={onNext}
      >
        Next
      </li>
    </ul>
  );
};

export default Pagination;
